import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';

function App(){
  const [q, setQ] = useState("");
  const tasks = [
    "Hur kan du pverka beslut i skolan eller framfra dina sikter i skolan?",
    "Hur kan du anvnda din yttrandefrihet och pverka i samhllet?",
    "Trots yttrandefrihet och demonstrationsfrihet r vissa saker olagligt att gra, vad?",
    "Var 4:e r r det val i Sverige dr alla med rstrtt fr rsta. I vilka tre val rstas det i?",
    "Var och nr kan man rsta nr det r valr?",
    "Vad r en folkomrstning?",
    "Beskriv vad EU och EU-parlamentet r.",
    "Ge exempel p vad staten, regioner och kommuner ansvarar fr.",
    "Vilka r riksdagens viktigaste uppgifter?",
    "Vad r regeringens viktigaste uppgifter?",
    "Vad r skillnaden p en monarki och en republik?"
  ]

  return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Felicias lxhjlp</h2>
        </div>
        <p className="App-intro">
        </p>
        <button onClick={() => setQ(tasks[Math.floor(Math.random()*tasks.length)])}>Ge mig en frga!</button>
<p>{q}</p>
      </div>
    );
  }

export default App;



